.orderDetails {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.orderDetailsId {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 0;
}

.orderDetailsIdValue {
  text-shadow: 0px 0px 16px rgba(51, 51, 255, 0.25),
               0px 0px 8px rgba(51, 51, 255, 0.25),
               0px 4px 32px rgba(51, 51, 255, 0.5);
}

.orderDetailsDone {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
}